/**
 * @WordPress dependencies
 */
import { RichText, InnerBlocks, useBlockProps } from '@wordpress/block-editor';
// import { renderToString } from '@wordpress/element';

/**
 * @Internal dependencies
 */
import ArkbSVG from '@components/deprecated/ArkbSVG';

/**
 * export deprecated
 */
const blockName = 'ark-block-accordion';
export default [
	// タブ操作できなかった問題修正 & アイコン調整前
	{
		supports: {
			anchor: true,
			className: false,
			reusable: false,
		},
		attributes: {
			title: {
				type: 'string',
				source: 'html',
				selector: '.ark-block-accordion__label',
				default: '',
			},
			iconOpened: {
				type: 'string',
				default: 'arkb-svg-minus',
			},
			iconClosed: {
				type: 'string',
				default: 'arkb-svg-plus',
			},
			isDefultOpen: {
				type: 'boolean',
				default: false,
			},
			// 本来はないattr. role img などがついていた頃かどうかをチェック
			svgRole: {
				type: 'string',
				source: 'attribute',
				selector: '.arkb-svg',
				attribute: 'role',
			},
		},
		save: ({ attributes }) => {
			const { title, iconOpened, iconClosed, isDefultOpen, svgRole } = attributes;

			const blockProps = useBlockProps.save({
				className: `${blockName}__item`,
				'aria-expanded': isDefultOpen ? 'true' : 'false',
			});

			const iconProps = svgRole ? { role: 'img', focusable: 'false' } : {};

			return (
				<div {...blockProps}>
					<div className={`${blockName}__title`} data-ark-acc>
						<span className={`${blockName}__label`}>
							<RichText.Content value={title} />
						</span>
						<span
							className={`${blockName}__icon`}
							aria-hidden='true'
							data-opened={isDefultOpen ? 'true' : 'false'}
						>
							<span className='__closed'>
								<ArkbSVG icon={iconClosed} size='24' {...iconProps} />
							</span>
							<span className='__opened'>
								<ArkbSVG icon={iconOpened} size='24' {...iconProps} />
							</span>
						</span>
					</div>

					<div
						className={`${blockName}__body ark-keep-mt--s`}
						aria-hidden={isDefultOpen ? 'false' : 'true'}
					>
						<InnerBlocks.Content />
					</div>
				</div>
			);
		},
	},
];
