/**
 * @WordPress dependencies
 */
import { __ } from '@wordpress/i18n';
import { registerBlockType } from '@wordpress/blocks';
import { InspectorControls, RichText, InnerBlocks, useBlockProps } from '@wordpress/block-editor';
import { PanelBody, ToggleControl } from '@wordpress/components';
import { useInnerBlocksProps } from '@compatible';

/**
 * @Internal dependencies
 */
import metadata from './block.json';
import deprecated from './deprecated';
import blockIcon from './icon';
import classnames from 'classnames';

/**
 * アコーディオン項目ブロック
 */
const blockName = 'ark-block-accordion';
registerBlockType(metadata.name, {
	title: __('Accordion item', 'arkhe-blocks'),
	icon: blockIcon,
	edit: ({ attributes, setAttributes }) => {
		const { isDefultOpen } = attributes;

		const blockProps = useBlockProps({
			className: `${blockName}__item is-opened`, // エディター側は常に開いておく
		});
		const innerBlocksProps = useInnerBlocksProps(
			{
				className: `${blockName}__body ark-keep-mt--s`,
			},
			{
				template: [['core/paragraph']],
				templateLock: false,
			}
		);

		return (
			<>
				<InspectorControls>
					<PanelBody title={__('Settings', 'arkhe-blocks')} initialOpen={true}>
						<ToggleControl
							label={__('Make it open by default', 'arkhe-blocks')}
							checked={isDefultOpen}
							onChange={(value) => {
								setAttributes({ isDefultOpen: value });
							}}
						/>
					</PanelBody>
				</InspectorControls>
				<div {...blockProps}>
					<div className={`${blockName}__title`}>
						<RichText
							tagName='div'
							className={`${blockName}__label`}
							placeholder={__('Enter text', 'arkhe-blocks') + '...'}
							value={attributes.title}
							onChange={(title) => setAttributes({ title })}
						/>
						<span className={`${blockName}__icon`} aria-hidden='true'></span>
					</div>
					<div {...innerBlocksProps} />
				</div>
			</>
		);
	},
	save: ({ attributes }) => {
		const { title, isDefultOpen } = attributes;

		const blockProps = useBlockProps.save({
			className: classnames(`${blockName}__item`, {
				'is-opened': isDefultOpen,
			}),
			open: isDefultOpen ? '' : null,
			'data-ark-acc': 'wrapper',
		});

		return (
			<details {...blockProps}>
				<summary className={`${blockName}__title`} data-ark-acc='header'>
					<span className={`${blockName}__label`}>
						<RichText.Content value={title} />
					</span>
					<span className={`${blockName}__icon`} aria-hidden='true'></span>
				</summary>
				<div className={`${blockName}__body ark-keep-mt--s`} data-ark-acc='body'>
					<InnerBlocks.Content />
				</div>
			</details>
		);
	},
	deprecated,
});
